import gql from 'graphql-tag'

export const photoSummaryFragment = gql`
fragment photoSummaryFragment on PhotoSummary {
  id
  takenOn
  seriesCreatedBy
  updatedOn
  createdOn
  seriesId
  createdUser {
    ...userSummaryFragment
  }
  paths {
    base
    thumb
    medium
    large
  }
}
`

export const userSummaryFragment = gql`
fragment userSummaryFragment on UserSummary {
  id
  gravatar
  gravatarProfile
  username
}
`

export const seriesSummaryFragment = gql`
fragment seriesSummaryFragment on SeriesSummary {
  id
  name
  access
  createdUser {
    ...userSummaryFragment
  }
  seriesStatus
  updatedOn
  createdOn
  photoCount
  coords
  direction
  watchers
  featured {
    ...photoSummaryFragment
  }
  latest {
    ...photoSummaryFragment
  }
}
${photoSummaryFragment}
${userSummaryFragment}
`

export const seriesFragment = gql`
fragment seriesFragment on Series {
  id
  name
  note
  meta
  watchers
  createdOn
  updatedOn
  createdUser {
    ...userSummaryFragment
  }
  updatedUser {
    ...userSummaryFragment
  }
  coords
  direction
  photoCount
  featured {
    ...photoSummaryFragment
  }
  latest {
    ...photoSummaryFragment
  }
}
${photoSummaryFragment}
${userSummaryFragment}
`

export const photoFragment = gql`
fragment photoFragment on Photo {
  id
  meta
  createdOn
  takenOn
  updatedOn
  coords
  seriesId
  seriesCreatedBy
  createdBy
  updatedBy
  createdUser {
    ...userSummaryFragment
  }
  updatedUser {
    ...userSummaryFragment
  }
  tags {
    label
    typeId
    coords {
      x
      y
    }
  }
  paths {
    base
    large
    thumb
    medium
  }
}
${userSummaryFragment}
`

export const userFragment = gql`
fragment userFragment on User {
  id
  username
  gravatar
  watching
  gravatarProfile
  meta
  createdOn
  updatedOn
}
`
