import gql from 'graphql-tag'
import { photoFragment, seriesFragment } from './fragments'

export const deletePhoto = gql`
mutation deletePhoto($seriesId: ID!, $photoId: ID!) {
  deletePhoto(seriesId: $seriesId, photoId: $photoId)
}
`

export const deleteSeries = gql`
mutation deleteSeries($id: ID!) {
  deleteSeries(id: $id)
}
`

export const updateSeries = gql`
mutation updateSeries($series: UpdateSeriesInput!) {
  updateSeries(series: $series){
    ...seriesFragment
  }
}
`

export const updatePhoto = gql`
mutation updatePhoto($photo: UpdatePhotoInput!) {
  updatePhoto(photo: $photo){
    ...photoFragment
    uploadToken
  }
}
`

export const createPhoto = gql`
mutation createPhoto($photo: NewPhotoInput!, $ext: String!) {
  createPhoto(photo: $photo, ext: $ext) {
    photo {
      ...photoFragment
      uploadToken
    }
    preAuth
  }
}
${photoFragment}
`

export const createSeries = gql`
mutation createSeries($series: NewSeriesInput!) {
  createSeries(series: $series) {
    ...seriesFragment
    photos {
      ...photoFragment
    }
  }
}
${seriesFragment}
${photoFragment}
`

export const watchSeries = gql`
mutation watch($seriesId: ID) {
  watchSeries(seriesId: $seriesId)
}
`

export const unWatchSeries = gql`
mutation unWatch($seriesId: ID) {
  unWatchSeries(seriesId: $seriesId)
}
`

export const reportBug = gql`
mutation reportBug($reportJSON: String!) {
  reportBug(reportJSON: $reportJSON)
}
`
