/**
 * Compose all our queries together
 */
const connectedWithApollo = compose(
  withGetSelectedQuery,
  withCreateNewSeries,
  withCreateNewPhoto,
  withUpdateSeriesMutation,
  withUpdatePhotoMutation,
  withDeleteSeriesDeleteMutation,
  withDeletePhotoMutation,
  withWatchSeriesMutation,
  withUnWatchSeriesMutation
)(SeriesConnect)

/**
 * Map our redux state and functions
 */
const mapStateToProps = (state, props) => {
  const { current: { activeSeries, initialPhoto, domain } } = state.home
  return { activeSeries, initialPhoto, domain }
}

const mapDispatchToProps = dispatch => ({
  onBack: () => {
    dispatch(actions.homeStateBack())
  },
  onAfterSeriesDelete: (key, newProgress) => {
    dispatch(actions.setHomeState(home.AppStateTypes.FEED))
  },
  queueUpload: (uploadObj) => {
    dispatch(actions.queueUpload(uploadObj))
  },
  doneCreatingSeries: (activeSeries) => {
    dispatch(actions.setHomeState(home.AppStateTypes.SERIES, { activeSeries }))
  }
})

export default connect(
  mapStateToProps,
  mapDispatchToProps
)(connectedWithApollo)

/**
 * We need to filter out null values and params we don't desire
 */
function sanitizeUpdateParams (inputObj, desiredParams) {
  const newDesiredParams = [...desiredParams, '__typename']
  const remainder = Object.keys(inputObj).filter(key => newDesiredParams.indexOf(key) > -1)
  console.log('sanitizeUpdateParams', { orig: Object.keys(inputObj), remainder })
  return remainder.reduce((acc, key) => ({ ...acc, [key]: inputObj[key] }), {})
}
